import { Component, Input } from '@angular/core';
import { PaymentSubmitStatuses } from '../../models';

@Component({
  selector: 'bb-payord-status-card',
  templateUrl: './status-card.component.html',
})
export class PayordStatusComponent {
  /**
   * Status of the payment. Defaults to success.
   */
  @Input() status: PaymentSubmitStatuses = PaymentSubmitStatuses.SUCCESS;
  /**
   * Message to be displayed. Defaults to empty string.
   */
  @Input() message = '';
  /**
   * Flag to denote debit mode.
   */
  @Input() isDebitPayment = false;
}
